    export default {
        name: "detail-attr",
	    data() {
            return {
                image_url: ''
            }
	    },
	    props: {
            height: Number,
            cover_pic: String,
            attr: Array,
            attr_groups: Array,
            goods_stock: Number,
            attr_swell_deposit: {
                type: [String, Number]
            },
            attr_deposit: {
                type: [String, Number]
            },
            attr_stock: {
                type: [String, Number]
            },
            price_member: Number,
            attr_price: String,
            level_show: Number,
            attr_price_member: Number,
            num: Number,
            attr_pic_url: String,
			theme: Object,
	    },
	    methods: {
            close_attr() {
                this.$emit('close_attr', true);
            },
            select_attr(data, item) {
                this.$emit('select_attr', {
                    data: data.attr_group_id, item: item.attr_id
                });
            },
            change_num(data) {
                this.$emit('change_num_data', Number(data.detail.value));
            },
		    add_num() {
                this.$emit('change_num', 1);
            },
		    edd_num() {
                if (this.num === 1) return;
                this.$emit('change_num', -1);
		    },
            clickImage() {
                let url = [];
                if (this.attr_pic_url) {
                    url.push(this.attr_pic_url);
                } else {
                    url.push(this.image_url);
                }
                uni.previewImage({
                    urls: url
                });
            },
			preventD() {}
	    },
	    mounted() {
            if (this.attr[0] && this.attr[0].pic_url) {
                this.image_url = this.attr[0].pic_url;
            } else {
	            this.image_url = this.cover_pic;
            }
	    }
    }
